.UserList{
    flex: 4;
}

.UserListUser{
    display: flex;
    align-items: center;

}

.userListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.userListDelete{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    color: red;
    cursor: pointer;

}
.userListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    color: #3bb077;
    cursor: pointer;
    margin-right: 20px;

}